    width: 40px;
    text-align: center;
    padding: 0.4rem;
}

.Number:hover{
    border: 1px solid #2B3BF6;
    color: #2B3BF6;
    background-color: rgba(43, 59, 246,0.1);
}

.SectionOneContentRight{
    z-index: 0;
    margin: 40px;
     padding: 0 20px 0 40px;
}
.SectionOneContentRightImage_mobileinside{
    position: absolute;
                  margin-left: -283px;
                  z-index: -1;
                  margin-top: 12px;
}
.SectionOneContentRightImage_mobilebg{
    position: absolute;
    margin-left: -400px;
    z-index: -2;
}

@media screen and (max-width: 1110px) {
