    }
    .SectionOneHeading{
        padding: 20px 20px 10px 20px;
    }
    
    
    
    .SectionOneParagraph{
        padding: 20px 20px 10px 20px;
    }
    
    
    
    .SectionOneContentLeftContainer{
        margin-top: 20px;
        align-items: baseline;
    }
    .SectionOneContentLeft{
        display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 20px 0 20px;

    }
    .Text{
        margin:0;
    }
    .SectionOneContentRight{
        margin:0;
        padding: 40px 0 20px 0;

    }
    .SectionOneContentRightImage_mobile{
        width:270px;
    }
    .SectionOneContentRightImage_mobileinside{
        margin-left: -193px;
        margin-top: 8px;
        width: 115px;

    }
    .SectionOneContentRightImage_mobilebg{
        width: 270px;
        margin-left: -278px;

    }
